/* global.css or index.css */

/* Prevent horizontal scroll & enable smooth font rendering */
html,
body {
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Install Button Animation */
#installBtn {
  animation: fadeSlideUp 0.6s ease forwards;
}
@keyframes fadeSlideUp {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Fixed header */
.site-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9999;
  backdrop-filter: blur(6px);
}

/* Content padding for fixed header */
main.content-with-fixed-header {
  padding-top: 4rem;
}

.hidden {
  display: none;
}

/* Toast container */
#toastContainer {
  position: fixed;
  bottom: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.toast {
  background: rgba(51, 51, 51, 0.9);
  color: #fff;
  padding: 0.75rem 1.25rem;
  border-radius: 9999px;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  opacity: 0;
  transform: translateY(20px);
  animation: slideInUp 0.3s forwards, fadeOut 0.3s forwards 2.7s;
  z-index: 99999;
}

@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeOut {
  to {
    opacity: 0;
    transform: translateY(-20px);
  }
}

/* Scroll animation */
.fade-up {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.animate-fadeIn {
  animation: fadeIn 0.2s ease-out;
}
.visible {
  opacity: 1;
  transform: translateY(0);
}

video,
img {
  border-radius: 10px;
  width: 100%;
}

/* Face recognition toast */
#toastFaceRec {
  position: fixed;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  background: #16a34a;
  color: white;
  padding: 10px 20px;
  border-radius: 12px;
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: 9999;
}

/* Loader and messages */
#loader,
#msg {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 18px;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/* Menu container */
.menu-scroll {
  display: flex;
  gap: 1.5rem;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  padding: 0 5%;
}
.menu-scroll::-webkit-scrollbar {
  display: none;
}

/* Menu card */
.menu-scroll > div {
  scroll-snap-align: center;
  flex: 0 0 14rem;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s ease, transform 0.8s ease;
}
.menu-scroll > div.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Responsive (mobile) */
@media (max-width: 768px) {
  .menu-scroll {
    padding-inline: 8%;
  }
  .menu-scroll > div {
    flex: 0 0 90%;
    max-width: 50%;
  }
  .left-arrow,
  .right-arrow {
    display: none;
  }
}

/* Arrows */
.left-arrow,
.right-arrow {
  z-index: 10;
  background-color: rgba(31, 41, 55, 0.85);
  color: white;
  border-radius: 9999px;
  padding: 0.45rem;
  transition: opacity 0.3s ease;
}
@media (min-width: 769px) {
  .left-arrow,
  .right-arrow {
    opacity: 0;
  }
  .group:hover .left-arrow,
  .group:hover .right-arrow {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-slideIn {
  animation: slideIn 0.3s ease-out;
}

